export default {
  props: {
    blockKey: String,
    blockDetail: Object,
    index: Number,
    remover: Function,
  },
  watch: {
    localFabric: {
      handler(newValue) {
        // Object.keys(newValue).forEach((key) => {
        //   this.localBlock[key] = newValue[key];
        // });
      },
      deep: true,
    },
    localFabric: {
      handler(newValue) {
        //    alert('well')
        this.$emit("input", newValue);
      },
      deep: true,
    },
  },
  mounted() {
    Object.keys(this.blockDetail).forEach((key) => {
      this.localFabric[key] = this.blockDetail[key];
    });
  },
  data() {
    return {
      localFabric: {
        key: "",
        name: "",
        mainImage: "",
        paragraph: "",
        subImage1: "",
        subImage2: "",
        subHeading1: "",
        subParagraph1: "",
        subHeading2: "",
        subParagraph2: "",
        subHeading3: "",
        subParagraph3: "",
      },
    };
  },
};
